.header {
  position: relative;
  z-index: 20;
  padding: 40px 0;
  @include media("<=810px") {
    
  }

  @include media("<=430px") {
    padding: 16px 0;
  }
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 59px 0 61px;
  @include media("<=1280px") {
    padding: 0 15px;
  }
}

.header__nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  max-width: 431px;
  width: 100%;
  @include media("<=810px") {
    display: none;
    opacity: 0;
    visibility: hidden;
    max-width: none;
    transition: all 0.3s linear;
    &.active {
      display: flex;
      position: fixed;
      height: 40%;
      width: 100%;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      padding-left: 55px;
      z-index: 20;
      display: flex;
      flex-direction: column;
      flex-wrap: nowrap;
      justify-content: center;
      align-content: space-between;
      align-items: flex-start;
      background-color: #ffffff;
      opacity: 1;
      visibility: visible;

      .header__nav-link {
        padding: 15px;
        margin-left: 0;
      }
    }
  }
  
}

.dropdown__button {
    cursor: pointer;
    border: none;
    background-color: transparent;
    position: relative;
    text-align: left;
    @include media("<=810px") {
      margin: 15px 0;
    }
    &::before {
      position: absolute;
      content: '';
      top: 0;
      right: 0;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 5px 5px 0 5px;
      border-color: #323642 transparent transparent transparent;
    }
    button {
      cursor: pointer;
      border: none;
      background-color: transparent;
    }
  }

  .dropdown__list {
    position: absolute;
    right: 0;
    top: calc(100% + 0.25rem);
    background-color: #ffffff;
    width: 100%;
    border-radius: 5px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
    &-item {
      padding: 10px;
    }
  }

.dropdown__button:hover .dropdown__list {
  opacity: 1;
  visibility: visible;
}  

.header__nav-link {
  position: relative;
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
  text-transform: uppercase;
  line-height: 1.1;
  letter-spacing: 1px;
  color: #323642;
  transition: all 0.3s linear;
  padding-right: 15px;
  @include media("<=1080px") {
    
  }
  @include media("<=810px") {

  }

}

.header__nav-link::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 3px;
  background-color: #4cb649;
  bottom: -5px;
  left: 0;
  transform: scale(0, 1);
  transition: transform 0.3s ease;
}

.header__nav-link:hover::after {
  transform: scale(1, 1);
}

.header__logo {
  max-width: 259px;
  display: block;
  @include media("<=1024px") {
    
  }
  @include media("<=810px") {
    margin-left: 55px;
    position: relative;
  }
  @include media("<=430px") {
    max-width: 168px;
    margin-left: 38px;
  }
  img {
    width: 100%;
    display: block;
  }
}

.header__buttons {
  max-width: 260px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  @include media("<=430px") {
    justify-content: flex-end;
    max-width: 126px;
  }
  .button:first-of-type {
    @include media("<=430px") {
      display: none;
    }
  }
}

.button {
  background-image: linear-gradient(to top, #4cb649, #5ddf5a, #ffffff, #ffffff);
  background-size: 100% 300%;
  color: #4cb649;
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  display: block;
  max-width: 160px;
  position: relative;
  z-index: 2;
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  padding: 16px 27px 8px;
  border-radius: 30px;
  border: 1px solid #4cb649;
  @include media("<=1024px") {
  }
  @include media("<=810px") {
    
  }
  @include media("<=430px") {
    position: relative;
    padding: 16px 1px 8px;
    width: 100%;
  }

  &:hover {
    color: #ffffff;
    background-position: 0 100%;
    moz-transition: all .4s ease-in-out;
    -o-transition: all .4s ease-in-out;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
   
  }
}

.warning {
  background-color: #22252e;
  padding: 10px 0 11px;
  position: relative;
}

.warning__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 610px;
  width: 100%;
  margin: 0 auto;
  @include media("<=430px") {
    flex-wrap: wrap;
    padding-right: 23px;
  }
  
}

.warning__message {
  display: flex;
  align-items: center;
  color: #ff8d00;
  font-size: 12px;
  font-weight: 700;
  @include media("<=330px") {
    font-size: 10px;
  }
  &-text {
    margin-left: 5px;
  }
  &-sign {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 11px 19px 11px;
    border-color: transparent transparent #ff8d00 transparent;
  }
}

.warning__info {
  color: #889099;
  font-weight: 400;
  font-size: 12px;
  display: flex;
  @include media("<=430px") {
    order: 3;
    padding-top: 10px;
  }
  @include media("<=330px") {
    font-size: 10px;
  }
}

.warning__link {
  color: #ffffff;
  text-decoration: none;
  font-size: 12px;
  font-weight: 400;
  @include media("<=430px") {
    order: 2;
  }
  &:hover {
    text-decoration: underline;
  }
}

.close {
  position: absolute;
  top: 13px;
  right: 21px;
  @include media("<=430px") {
    right: 15px;
  }
}