.footer {
  padding: 72px 0 91px;
  @include media("<=810px") {
    padding: 47px 0 20px;
  }
  @include media("<=430px") {
    
  }
}

.footer__logo {
  display: block;
  max-width: 239px;
  img {
    width: 100%;
  }
}

.footer__inner {
  display: flex;
  justify-content: space-between;
  padding-top: 77px;
  @include media("<=810px") {
    flex-wrap: wrap;
    padding-top: 47px;
  }
}

.footer__column {
  flex: 0 1 100%;
  max-width: 164px;
  &.about {
    max-width: 600px;
    display: flex;
    flex-direction: column;
  }
}

.footer__column-title {
  color: #323642;
  font-weight: 500;
  font-size: 14px;
  text-transform: uppercase;
  padding-bottom: 21px;
}

.footer__column-block {
  margin-bottom: 48px;
  @include media("<=430px") {
    order: 2;
  }
}

.footer__list {
  &-item {
    padding-bottom: 21px;
  }
  &-link {
    color: #889099;
    text-decoration: none;
    font-size: 14px;
    font-weight: 400;
    &:hover {
      text-decoration: underline;
    }
  }
}

.footer__column-about {
    color: #889099;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.5;
    
}

.footer__column-text {
  color: #889099;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.3;
  padding-bottom: 20px;

}

.footer__social {
@include media("<=430px") {
    order: 1;
    padding-bottom: 20px;
  }
}

.footer__social-inner {
  max-width: 333px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  
}