body {
  margin: 0;
  font-family: 'Roboto',
  sans-serif;
  background-color: #ffffff;
  color: #22252e;
  position: relative;
  &.page {
    background-image: none;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

.container {
  width: 100%;
  max-width: 1230px;
  padding: 0 15px;
  margin: 0 auto;
}

.section-title {
  color: #ffffff;
  text-align: center;
  font-size: 48px;
  position: relative;
  font-weight: 300;
  z-index: 4;
  @include media("<=810px") {
    font-size: 42px;
  }
  @include media("<=430px") {
    font-size: 32px;
  }
}

//BURGER BUTTON//
.burger__button {
  display: none;

  @include media("<=tablet") {
    position: fixed;
    display: block;
    top: 88px;
    
    z-index: 200;
  }

  @include media("<=430px") {
    top: 92px;
    
  }

  @include media("<=tablet") {
    &.active {
      position: fixed;
      top: 88px;
      
      z-index: 200;
    }
    @include media("<=430px") {
      &.active {
        top: 92px;
        
      }
    }
  }

  .line {
    height: 4px;
    width: 40px;
    margin-bottom: 6px;
    background: #555;
    transition: all 0.4s ease-in-out;
    transform-origin: 0 0;
    opacity: 1;
    @include media("<=430px") {
      width: 24px;
      height: 2px;
      margin-bottom: 4px;
      transform-origin: top left;
    }
  }

  &.active {
    .line {
      &.one {
        transform: rotate(45deg) translate(1px, -6px);
        @include media("<=430px") {
          transform: rotate(45deg) translate(1px, -4px);
        }
      }
      &.two {
        transform: scaleX(0);
      }
      &.three {
        transform: rotate(-45deg) translate(-2px, 6px);
        @include media("<=430px") {
          transform: rotate(-45deg) translate(-1px, 4px);
        }
      }
    }
  }
}

