.blog {
  padding: 40px 0 73px;
}

.blog__inner {
  display: flex;
  justify-content: space-between;
  @include media("<=430px") {
    flex-direction: column;
    align-items: center;
    max-width: 600px;
    margin: 0 auto;
  }
  .button {
    display: block;
    margin: 40px auto 0;
    max-width: 260px;
    width: 100%;
  }
}

.blog__posts {
  flex: 0 1 100%;
  max-width: 800px;
  @include media("<=1080px") {
    padding-right: 10px;
  }
  @include media("<=430px") {
    max-width: 315px;
    padding-right: 0;
    display: flex;
    flex-direction: column;
  }
}

.blog__title {
  font-size: 32px;
  color: #22252e;
  margin-bottom: 11px;
}

.blog__item {
  max-width: 780px;
  padding: 30px 0;
  display: flex;
  position: relative;
  @include media("<=430px") {
    flex-direction: column;
  }
  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    height: 1px;
    width: 100%;
    background-color: #ebeced;
  }
  &:last-of-type, &:nth-of-type(4), &:nth-of-type(5) {
    @include media("<=430px") {
      display: none;
    }
  }
}

.blog__item-image {
  flex: 0 1 100%;
  max-width: 280px;
  @include media("<=430px") {
    margin-bottom: 23px;
  }
  img {
    width: 100%;
    display: block;
  }
}

.blog__item-content {
  flex: 0 1 100%;
  max-width: 460px;
  padding-left: 40px;
  @include media("<=810px") {
    padding-left: 15px;
  }
  @include media("<=430px") {
    padding-left: 0;
  }
}

.content__title a {
  display: block;
  text-decoration: none;
  font-size: 24px;
  line-height: 1.3;
  margin-bottom: 15px;
  color: #22252e;
  &:hover {
    text-decoration: underline;
  }
}

.aside {
  position: relative;
  flex: 0 1 100%;
  max-width: 400px;
  display: flex;
  flex-direction: column;
  padding-left: 59px;
  @include media("<=1080px") {
    padding-left: 30px;
    max-width: 320px;
  }
  @include media("<=810px") {
    max-width: 265px;
  }
  @include media("<=430px") {
    max-width: 320px;
    padding-left: 0;
  }
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 1px;
    height: 98%;
    background-color: #ebeced;
    @include media("<=430px") {
      display: none;
    }
  }
  hr {
    max-width: 340px;
    width: 100%;
    height: 1px;
    color: #ebeced;
    border-top: none;
    margin: 13px 0 0;
  }
}

.search {
  position: relative;
  display: block;
  max-width: 340px;
  width: 100%;
  @include media("<=430px") {
    display: none;
  }
  &-input {
    max-width: 340px;
    width: 100%;
    padding: 20px 0 18px 58px;
    color: #22252e;
    font-size: 18px;
    border-radius: 4px;
    border: 1px solid #d7d8d9;
    &::placeholder {
      font-family: 'Roboto',
      sans-serif;
      color: #22252e;
      font-size: 18px;
    }
  }
  &-btn {
    border: none;
    background-color: transparent;
    position: absolute;
    top: 21px;
    left: 20px;
    cursor: pointer;
  }
  &.search-blog {
    display: none;
    @include media("<=430px") {
      display: block;
      margin-left: 0;
      margin-bottom: 20px;
    }
  }
}

.blog__ads, .aside__posts {
  padding-top: 42px;
  max-width: 341px;
}

.aside__title {
  display: flex;
  align-items: center;
  color: #22252e;
  font-size: 24px;
  img {
    max-width: 40px;
    width: 100%;
    display: block;
    margin: 0 30px 0 10px;
    @include media("<=810px") {
      margin-right: 15px;
    }
  }
}

.blog__ads-list, .aside__posts-list {
  box-sizing: border-box;
  list-style: initial;
  padding-top: 34px;
  max-width: 341px;
}

.blog__ads-item, .aside__posts-item {
  margin-left: 10px;
  margin-bottom: 21px;
  font-size: 14px;
  font-weight: 400;
  color: #4cb649;
  max-width: 330px;
  a {
    color: #323642;
    text-decoration: none;
    line-height: 1.3;
    &:hover {
      text-decoration: underline;
    }
  }
}

.aside__banner {
  max-width: 340px;
  margin: 32px 0 55px;
img {
  width: 100%;
  display: block;
}
}

.tagcloud__list {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  padding-top: 24px;
}

.tagcloud__list-item a {
  display: block;
  position: relative;
  text-decoration: none;
  color: #22252e;
  padding-right: 4px;
  line-height: 1.3;
  font-size: calc(18px * var(--tag-multiplier));
  opacity: calc(0.8 * var(--tag-multiplier));
  &:hover {
    text-decoration: underline;
  }
}