.main {
  position: relative;
  padding-bottom: 30px;
  @include media("<=1080px") {
    
  }
  @include media("<=430px") {
    padding: 30px 0 0;
  }
  @include media("<=375px") {
  }
  @include media("<=320px") {
  }
}

.main__inner {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 6px;
  border: 1px solid #d7d8d9;
  @include media("<=1024px") {
  }
  @include media("<=810px") {
   flex-direction: column;
   max-width: 600px;
   margin: 0 auto;
  }
  @include media("<=430px") {
    
  }
}

.main__image {
  flex: 0 1 100%;
  max-width: 600px;
  img {
    width: 100%;
    display: block;
  }
}

.main__content {
  flex: 0 1 100%;
  max-width: 600px;
  padding: 0 60px;
  display: flex;
  flex-direction: column;
  @include media("<=810px") {
    padding: 34px 30px 16px;
  }
}

.main__content-title a {
  font-size: 24px;
  line-height: 1.3;
  margin-bottom: 90px;
  color: #22252e;
  display: block;
  text-decoration: none;
  @include media("<=1080px") {
    margin-bottom: 50px;
  }
  @include media("<=430px") {
    margin-bottom: 20px;
  }
  &:hover {
    text-decoration: underline;
  }
}

.content__info {
  margin-bottom: 16px;
  color: #889099;
  font-size: 14px;
}

.content__info-author {
  color: #323642;
  font-size: 14px;
  font-weight: 400;
  text-decoration: none;
}

.content__tags {
  margin-bottom: 15px;
  &-link {
    color: #4cb649;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 400;
  }
}

.content__comments {
  text-decoration: none;
  font-size: 14px;
  font-weight: 400;
  color: #889099;
  &-icon {
    margin: 0 10px 0 5px;
    max-width: 12px;
    width: 100%;
  }
}