.tags__inner {
    border-radius: 4px;  
    background-color: #f7f7f7;
    padding: 26px 32px 18px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    @include media("<=810px") {
        display: none;
    }
}

.tags__content {
    display: flex;
    align-items: center;
}

.tags__title {
    margin-right: 42px;
    color: #323642;
    font-size: 14px;
    font-weight: 500;
    text-transform: uppercase;
}

.tags__item {
    color: #4cb649;
    font-size: 14px;
    font-weight: 500;
    text-transform: uppercase;
    text-decoration: none;
    margin-right: 36px;
    &:hover {
        text-decoration: underline;
    }
}

.tags__links {
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-item {
        color: #323642;
        font-size: 14px;
        font-weight: 500;
        text-transform: uppercase;
        text-decoration: none;
        margin-left: 38px;
        &:hover {
            text-decoration: underline;
        }
    }
}