.plans {
  padding: 106px 0 54px;
  background: linear-gradient(117deg, #3d4251 0%, #272b35 100%);
  @include media("<=810px") {
    padding: 65px 0 41px;
  }
}

.plans__inner {
  padding: 62px 0 31px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  @include media("<=810px") {
    flex-direction: column;
  }
}

.plans__item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: space-between;
  border-radius: 6px;
  border: 1px solid #ebeced;
  background-color: #ffffff;
  padding: 53px 0 40px;
  max-width: 380px;
  width: 100%;
  flex: 0 1 33%;
  min-height: 340px;
  height: 100%;
  transition: all 0.3s ease;
  @include media("<=810px") {
    margin-bottom: 20px;
  }
  &:hover {
    padding: 56px 0 48px;
    transform: scale(1.09);
    border: 4px solid #4cb649;
  }
  .button {
    max-width: 205px;
    width: 100%;
    display: block;
  }
}

.plans__item-title {
  flex: 0 1 auto;
  position: relative;
  color: #323642;
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
  &::after {
    content: "";
    position: absolute;
    width: 30%;
    height: 3px;
    background-color: #4cb649;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
  }
}

.plans__item-price {
color: #22252e;
font-size: 56px;
font-weight: 300;
letter-spacing: -1.5px;
display: flex;
align-items: flex-end;
line-height: 0.6;
position: relative;
span {
  font-size: 24px;
}
.currency {
  font-size: 24px;
  align-self: flex-start;
  margin-right: 8px;
}
&::after {
  content: '';
  position: absolute;
  bottom: -20px;
  width: 100%;
  max-width: 200px;
height: 1px;
background-color: #ebeced;
}
}

.plans__item-text {
  font-size: 14px;
  font-weight: 400;
  color: #323642;
  span {
    text-decoration: line-through;
    color: #f24458;
  }
  p {
    font-weight: 700;
    padding-top: 10px;
  }
}

.plans__pay {
  max-width: 916px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 15px;
  @include media("<=430px") {
   flex-wrap: wrap;
   justify-content: space-around;
   padding: 0 23px;
  }
  &-title {
    color: #ffffff;
    font-size: 18px;
    @include media("<=430px") {
      text-align: center;
      flex: 1 0 100%;
    }
  }
}

.plans__pay-item {
  @include media("<=430px") {
    margin-bottom: 10px;
  }
}

.plans__links {
  max-width: 451px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  @include media("<=430px") {
    flex-direction: column;
  }
}

.plans__links-item {
  color: #4cb649;
  font-weight: 500;
  font-size: 14px;
  text-decoration: none;
  text-transform: uppercase;
  display: flex;
  @include media("<=430px") {
    width: 100%;
    margin-top: 20px;
  }
  img {
    margin-left: 17px;
    display: block;
  }
  &:hover {
    text-decoration: underline;
  }
}